import React, { FC, MouseEvent, useEffect } from 'react'
import ReactTooltip from 'react-tooltip'
import clsx from 'clsx'
import { TooltipProps, TooltipStateEnum } from './types'
import { Condition } from '../Condition'
import './Tooltip.scss'

const EVENT_MOUSE_ENTER = 'mouseenter'
const EVENTMOUSE_LEAVE = 'mouseleave'
const EVENT_CLICK = 'click'
const TYPE_HOVER = 'hover'
const TYPE_CLICK = 'click'
const DOUBLE_CLICK = 'dblclick'

const Tooltip: FC<TooltipProps> = ({
  children,
  eventType,
  overlay,
  isTooltip = true,
  theme,
  ...rest
}) => {
  const hiddenByClickOrHover =
    eventType === EVENT_CLICK ? TYPE_CLICK : EVENTMOUSE_LEAVE

  const arrowFill = theme === TooltipStateEnum.DARK ? '#464C53' : '#FFFFFF'

  useEffect(() => {
    if (isTooltip) {
      ReactTooltip.rebuild()
    }
  }, [isTooltip])

  const onClick = (evt: MouseEvent<HTMLDivElement>): void => {
    evt.preventDefault()
    evt.stopPropagation()
    if (rest.afterShow) {
      rest.afterShow()
    }
  }

  return (
    <div>
      <span
        data-event={eventType === TYPE_HOVER ? EVENT_MOUSE_ENTER : EVENT_CLICK}
        data-event-off={
          eventType === EVENT_CLICK ? DOUBLE_CLICK : EVENTMOUSE_LEAVE
        }
        data-for={rest.id}
        data-tip
      >
        {children}
      </span>
      <Condition match={isTooltip}>
        <ReactTooltip
          {...rest}
          afterShow={onClick}
          arrowColor={arrowFill}
          className={clsx(`TechnoTooltip`, {
            '--dark': theme === TooltipStateEnum.DARK,
            '--light': theme === TooltipStateEnum.LIGHT,
          })}
          clickable={eventType === TYPE_CLICK}
          eventOff={hiddenByClickOrHover}
          globalEventOff={hiddenByClickOrHover}
          type={theme}
        >
          {overlay}
        </ReactTooltip>
      </Condition>
    </div>
  )
}

Tooltip.defaultProps = {
  theme: TooltipStateEnum.DARK,
  arrowColor: '#464C53',
  scrollHide: true,
  resizeHide: true,
  place: 'left',
  effect: 'solid',
  border: false,
  multiline: true,
  eventType: TYPE_CLICK,
}

export default Tooltip
